@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@media only screen and (max-width: 600px){.main__description--title{font-size:30px}.footer{align-items:flex-start;flex-direction:column}}body{margin:0;font-family:"Muli",sans-serif;background:#000}*{box-sizing:border-box}

.main{display:flex;align-items:center;justify-content:center;flex-direction:column;height:300px;border-radius:0px 0px 40px 40px}.main .isHome{width:70%}.main__title{color:white;font-size:25px}.input{background-color:rgba(255,255,255,0.1);border:2px solid white;border-radius:25px;color:white;font-family:'Muli', sans-serif;font-size:16px;height:50px;margin-bottom:20px;outline:none;padding:0px 20px}::-webkit-input-placeholder{color:white}:-ms-input-placeholder{color:white}::-ms-input-placeholder{color:white}::placeholder{color:white}

.categories__title{background:#000;color:white;font-size:16px;margin-top:-16px;position:absolute;padding-left:30px;width:100%}

.carousel{overflow:scroll;padding-left:30px;width:100%;position:relative}::-webkit-scrollbar{display:none}.carousel__container{transition:450ms transform;font-size:0;white-space:nowrap;margin:70px 0px;padding-bottom:10px}.carousel__container:hover .carousel-item{opacity:0.3}.carousel__container:hover .carousel-item:hover{transform:scale(1.5);opacity:1}

.carousel-item{border-radius:20px;overflow:hidden;position:relative;display:inline-block;width:200px;height:250px;margin-right:10px;font-size:20px;cursor:pointer;transition:450ms all;transform-origin:center left}.carousel-item:hover ~ .carousel-item{transform:translate3d(100px, 0, 0)}.carousel-item__img{width:200px;height:250px;object-fit:cover}.carousel-item__details{align-items:flex-start;background:linear-gradient(to top, rgba(0,0,0,0.9) 0%, rgba(0,0,0,0) 100%);bottom:0;display:flex;font-size:10px;flex-direction:column;justify-content:flex-end;left:0;opacity:0;transition:450ms opacity;padding:10px;position:absolute;right:0;top:0}.carousel-item__details--img{width:20px}.carousel-item:hover .carousel-item__details{opacity:1}.carousel-item__details--title{color:white;margin:5px 0px 0px 0px}.carousel-item__details--subtitle{color:white;font-size:8px;margin:3px 0px}

.header{align-items:center;background:#000;color:white;display:flex;height:100px;justify-content:space-between;top:0px;width:100%}.isLogin,.isRegister{background-color:#21c08b}.header__img{margin-left:30px;width:200px}.header__menu{margin-right:30px}.header__menu ul{display:none;list-style:none;margin:0px 0px 0px -14px;padding:0px;position:absolute;width:100px;text-align:right}.header__menu:hover ul,ul:hover{display:block}.header__menu li{margin:10px 0px}.header__menu li a{color:white;text-decoration:none}.header__menu li a:hover{text-decoration:underline}.header__menu--profile{align-items:center;display:flex;cursor:pointer}.header__menu--profile img{margin-right:8px;width:40px;border-radius:100%;border:2px solid white;object-fit:cover}

.login{background-image:linear-gradient(#21c08b, #000);display:flex;align-items:center;flex-direction:column;justify-content:center;padding:0px 30px;min-height:calc(100vh - 200px)}.login__container{background-color:rgba(255,255,255,0.1);border:2px solid white;border-radius:40px;box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22);color:white;display:flex;flex-direction:column;justify-content:space-around;padding:60px 68px 40px}.login__container--form{display:flex;flex-direction:column}.login__container--form label{font-size:14px}.login__container--remember-me{color:white;display:flex;justify-content:space-between;margin-top:10px}.login__container--remember-me a{color:white;font-size:14px;text-decoration:none}.login__container--remember-me a:hover{text-decoration:underline}.login__container--social-media>div{align-items:center;display:flex;font-size:14px;margin-bottom:10px}.login__container--social-media>div>img{margin-right:10px;width:30px}.login__container--register{font-size:14px}.login__container--register a{color:white;font-weight:bold;font-size:16px;text-decoration:none}.login__container--register a:hover{text-decoration:underline}.button{background-color:rgba(255,255,255,0.3);border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px}@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;box-shadow:none;padding:0px;width:100%}}

.register{background-image:linear-gradient(#21c08b, #000);display:flex;align-items:center;flex-direction:column;justify-content:center;padding:0px 30px;min-height:calc(100vh - 200px)}.register__container{background-color:rgba(255,255,255,0.1);border:2px solid white;border-radius:40px;box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22);color:white;display:flex;flex-direction:column;justify-content:space-around;padding:60px 68px 40px}.register__container--form{display:flex;flex-direction:column}.register__container a{color:white;font-size:16px;text-align:center;text-decoration:none}.register__container a:hover{text-decoration:underline}.button{background-color:rgba(255,255,255,0.3);border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px}@media only screen and (max-width: 600px){.register__container{background-color:transparent;border:none;box-shadow:none;padding:0px;width:100%}}

.footer{align-items:center;background-color:#000;display:flex;height:100px;width:100%}.footer a{color:white;cursor:pointer;font-size:14px;padding-left:30px;text-decoration:none}.footer a:hover{text-decoration:underline}

video{width:100vw;height:100vh;z-index:0;position:absolute;top:0;left:0;background-color:white}video:hover .Player-back{display:block}.Player-back{position:fixed;top:5px;left:5px;font-size:12px}.Player-back button{text-decoration:none;color:white;background:rgba(0,0,0,0.3);padding:5px;border-radius:5px;border:0;outline:0;cursor:pointer}

